.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
  cursor: pointer;

}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 170px;
  background-color: darkgreen;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  font-size: 20px !important;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;

  /*set location:  these settings set it to show on bottom*/
  top: 100%;
  left: 50%;
  margin-left: -130px; /* Use half of the width (120/2 = 60), to center the tooltip */
}

.tooltip .tooltiptextright {
  visibility: hidden;
  width: 400px;
  background-color: darkgreen;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  font-size: 20px !important;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;

  /*set location:  these settings set it to show on bottom*/
  top: 100%;
  left: 50%;
  margin-left: -10px; /* Use half of the width (120/2 = 60), to center the tooltip */
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.tooltip:hover .tooltiptextright {
  visibility: visible;
}


