.tileDisplay {
  width: 80vmin;
  height: 100%;
  float: left;
  padding-left: 30vmin !important;
}

.legendDisplay {
  width: 180px;
  height: 110px;
  float: left;
  padding: 0;
  margin: 10px 10px 10px 10px !important;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background: #222;
  font-family: Georgia, serif;
  color: #fff;
  font-size: 14px;
}

a {
  color: #ffffcc;
  text-decoration: none;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
  a:hover,
  a.selected {
    color: #ffcc00;
  }

h1,h2,h4,h5,h6 {
  text-align: center;
  color: #ccc;
  text-shadow: #000 1px 1px 2px;
  margin-bottom: 5px;
}
  h1 {
    font-size: 18px;
  }
  h2 {
    font-size: 14px;
  }
.sociales {
  text-align: center;
  margin-bottom: 20px;
}

  #timeline {
    width: 90vw;
    height: 350px;
    overflow: hidden;
    //margin: 100px auto;
    margin: 0px auto;
    padding-bottom: 100px;
    position: relative;
    background: url('../images/dot.gif') left 45px repeat-x;
  }
    #dates {
      width: 90vw;
      margin-left: 15vw;
      height: 60px;
      overflow: hidden;
    }
      #dates li {
        list-style: none;
        float: left;
        width: 200px;
        height: 50px;
        font-size: 24px;
        text-align: center;
        background: url('../images/biggerdot.png') center bottom no-repeat;
      }
        #dates a {
          line-height: 38px;
          padding-bottom: 10px;
        }
        #dates .selected {
              font-size: 38px;
        }
    
    #issues {
      width: 800px;
      height: 350px;
      overflow: hidden;
    } 
      #issues li {
        //width: 800px;
        width: calc(10px + 90vw);
        height: 350px;
        list-style: none;
        float: left;
      }
        #issues li.selected img {
          //-webkit-transform: scale(1.1,1.1);
          //transform: scale(1.1,1.1);
        }
        #issues li img {
          float: left;
          //margin: 10px 30px 10px 50px;
          margin:  10px 10px 10px calc(10px + 12vmin);
          background: transparent;
          -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr=#00FFFFFF,endColorstr=#00FFFFFF)"; /* IE 8 */   
          filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#00FFFFFF,endColorstr=#00FFFFFF);/* IE 6 & 7 */      
          //zoom: 1;
          //-webkit-transition: all 2s ease-in-out;
          //transition: all 2s ease-in-out;
          //-webkit-transform: scale(0.7,0.7);
          //transform: scale(0.7,0.7);
        }
        #issues li h1 {
          color: #ffcc00;
          font-size: 48px;
          margin: 20px 0;
          text-shadow: #000 1px 1px 2px;
        }
        #issues li p {
          font-size: 14px;
          margin-right: 70px;
          font-weight: normal;
          line-height: 22px;
          text-shadow: #000 1px 1px 2px;
        }
    
    #grad_left,
    #grad_right {
      width: 100px;
      height: 60px;
      position: absolute;
      top: 0;
    }
      #grad_left {
            left: 0;
            background: url('../images/grad_left.png') repeat-y;
      }
      #grad_right {
            right: 0;
            background: url('../images/grad_right.png') repeat-y;
      }
    
    #next,
    #prev {
      position: absolute;
      top: 0;
      font-size: 70px;
      top: 170px;
      width: 22px;
      height: 38px;
      background-position: 0 0;
      background-repeat: no-repeat;
      text-indent: -9999px;
      overflow: hidden;
    }
      #next:hover,
      #prev:hover {
        background-position: 0 -76px;
      }
      #next {
        right: 0;
        background-image: url('../images/next.png');
      }
      #prev {
        left: 0;
        background-image: url('../images/prev.png');
      }
        #next.disabled,
        #prev.disabled {
          opacity: 0.2;
        }
